import React from "react";
import { Switch } from "react-router-dom";
import PropTypes from "prop-types";

import Import from "../pages/settings/import";
import EditImport from "../pages/settings/editImport";

// Components
import PrivateRoute from "../components/PrivateRoute";

const ImportRoute = ({ match: { url } }) => (
  <>
    <Switch>
      <PrivateRoute path={`${url}/:id`} component={EditImport} />
      <PrivateRoute path={`${url}`} component={Import} />
    </Switch>
  </>
);

ImportRoute.propTypes = {
  match: PropTypes.shape({
    url: PropTypes.string,
  }),
};
export default ImportRoute;
