import React, { useState } from "react";
import { Row, Col, Form, Table, Input, Radio, Button } from "antd";
import styled from "styled-components";
import { useTranslation } from "react-i18next";
import PropTypes from "prop-types";

// Mock Data
import { permissonOptions } from "../../mockData";

export default function PermissonTable({
  dataSource,
  columns,
  loading,
  dynamicColumns,
  filters,
  onChangePermissions,
}) {
  const [t] = useTranslation();
  const [data, setData] = useState(dataSource);
  const [isModalVisible, setIsModalVisible] = useState(false);

  function getColumnsData(columns) {
    return [
      ...columns,
      {
        title: "Permisson",
        render(text, row, index) {
          return (
            <Radio.Group
              value={getValuePermissions(row.HasEdit, row.HasView)}
              onChange={(e) => permissonChange(e, row, index)}
            >
              <Row gutter={8}>
                {permissonOptions.map(({ label, value }) => (
                  <Col key={value}>
                    <Radio value={value}>{label}</Radio>
                  </Col>
                ))}
              </Row>
            </Radio.Group>
          );
        },
      },
    ];
  }

  function getValuePermissions(HasEdit, HasView) {
    if (HasEdit == null && HasView == null) {
      return "notChoice";
    }

    if (HasEdit == false && HasView == false) {
      return "notShow";
    }

    if (HasEdit == false && HasView == true) {
      return "onlyRead";
    }

    if (HasEdit == true && HasView == true) {
      return "readAndWrite";
    }
  }

  function permissonChange(e, row, index) {
    console.log(row);
    data[index] = {
      ...row,
      HasView: getDataHasView(e.target.value),
      HasEdit: getDataHasEdit(e.target.value),
    };
    setData([...data]);
    onChangePermissions([...data]);
  }

  function getDataHasView(value) {
    switch (value) {
      case "notShow":
        return false;
      case "onlyRead":
        return true;
      case "readAndWrite":
        return true;
      default:
        return null;
    }
  }

  function getDataHasEdit(value) {
    switch (value) {
      case "notShow":
        return false;
      case "onlyRead":
        return false;
      case "readAndWrite":
        return true;
      default:
        return null;
    }
  }

  return (
    <>
      <Row>
        <Col span={12}>
          <FilterWrapper>
            {dynamicColumns && (
              <Button
                onClick={() => setIsModalVisible(true)}
                style={{ marginRight: 10 }}
              >
                {t("Columns")}
              </Button>
            )}
            {filters}
          </FilterWrapper>
        </Col>
      </Row>
      <Table
        loading={loading}
        columns={getColumnsData(columns)}
        dataSource={data}
      />
    </>
  );
}
PermissonTable.propTypes = {
  dataSource: PropTypes.any,
  columns: PropTypes.any,
  onChangePermissions: PropTypes.func,
  loading: PropTypes.bool,
  filters: PropTypes.bool,
  dynamicColumns: PropTypes.bool,
};
const FilterWrapper = styled.div`
  margin-bottom: 20px;
  display: flex;
`;
