import React, { useState, useEffect } from "react";
import { useTranslation } from "react-i18next";
import { Form, Row, Col, Input, Space, Select, message } from "antd";
import { useHistory } from "react-router-dom";

// Components
import DefaultLayout from "../../../components/Layouts/Default";
import Container from "../../../components/Layouts/Container";
import Content from "../../../components/Content";
import BreadcrumbNav from "../../../components/BreadcrumbNav";
import DefaultTable from "../../../components/Table/DefaultTable";

// Components
import { RolesList } from "../../../services/roles";

//Services
import { ItemTypes } from "../../../services/items";

// Mock Data
import { importTable } from "../../../mockData";

export default function Import() {
  let history = useHistory();
  const { t } = useTranslation();
  const [loading, setLoading] = useState(true);
  const [itemTypesList, setItemsType] = useState([]);
  const [importList, setImportList] = useState([]);
  const [filters, setFilters] = useState({});
  const [pagination, setPagination] = useState({
    current: 1,
    pageSize: 10,
  });

  const getColumns = () => [
    {
      title: t("Type"),
      dataIndex: "TypeFile",
      key: "TypeFile",
    },
    {
      title: t("Name"),
      dataIndex: "FileName",
      key: "FileName",
    },
    {
      title: t("Date"),
      dataIndex: "CreateDate",
      key: "CreateDate",
    },
    {
      title: t("Status"),
      dataIndex: "StatusName",
      key: "StatusName",
    },
  ];

  const getItemsTypes = async () => {
    try {
      const result = await ItemTypes();

      setItemsType(result.data.Value);
      itemTypesList;
    } catch (err) {
      setLoading(false);
    }
  };

  const getResourceImport = async (pagination) => {
    const { PageSize, CurrentPage, data } = pagination;
    setLoading(true);

    try {
      const result = await RolesList({
        PageSize,
        CurrentPage: CurrentPage - 1,
        ...filters,
      });

      setImportList(result.data.data);
      setPagination({ ...data, total: result.data.recordsTotal });
      setLoading(false);
    } catch (err) {
      setLoading(false);
    }
  };

  const handleTableChange = async (pagination) => {
    const { current, pageSize } = pagination;
    await getResourceImport({
      PageSize: pageSize,
      CurrentPage: current,
      ...filters,
    });
  };

  const filterChange = (filter) => {
    const { target } = filter;
    setFilters({ ...filters, [target.name]: target.value });
  };

  const onEdit = ({ Id }) => {
    history.push(`/import/${Id}`);
  };

  useEffect(() => {
    getResourceImport({
      PageSize: 10,
      CurrentPage: 1,
    });

    getItemsTypes();
  }, [filters]);

  return (
    <DefaultLayout>
      <Container>
        <BreadcrumbNav
          navs={[{ label: t("Settings") }, { label: t("Import"), path: "#" }]}
        ></BreadcrumbNav>
      </Container>
      <Container>
        <Content title={t("Import")}>
          <Row>
            <Col>
              <Form.Item>
                <Select placeholder="Select...">
                  {Object.keys(itemTypesList).map((code) => (
                    <Select.Option key={code} value={code}>
                      {code}
                    </Select.Option>
                  ))}
                </Select>
              </Form.Item>
            </Col>
          </Row>

          <Row>
            <Col span={24}>
              <DefaultTable
                columns={getColumns()}
                onReset={() => setFilters({})}
                dataSource={importTable}
                loading={loading}
                style={{ marginTop: "15px" }}
                scroll={{ x: "1000px" }}
                actions
                onEdit={onEdit}
                pagination={pagination}
                onChange={handleTableChange}
                filters={
                  <Space size={10}>
                    <Input
                      name="RoleName"
                      placeholder={t("Name")}
                      value={filters.RoleName}
                      style={{ width: 200 }}
                      onChange={(val) => filterChange(val)}
                    />
                  </Space>
                }
              />
            </Col>
          </Row>
        </Content>
      </Container>
    </DefaultLayout>
  );
}
