import React from "react";
import { Row, Col, Space } from "antd";
import styled from "styled-components";
import PropTypes from "prop-types";
import { useTranslation } from "react-i18next";
import CustomPanel from "./CustomPanel";

const HistoryItem = ({ bgColor, history }) => {
  const [t] = useTranslation();
  const {
    UserName,
    Comment,
    OldValues,
    NewValues,
    LoggedAt,
    Version,
  } = history;

  return (
    <CustomPanel
      version={Version}
      label1={`${UserName}`}
      label2={`${LoggedAt}`}
    >
      <Row
        gutter={[10, 10]}
        style={{ backgroundColor: bgColor, padding: 10, paddingLeft: 20 }}
      >
        <Col span={24}>
          <Space size={10}>
            <Title></Title>
          </Space>
        </Col>{" "}
        <Col
          onClick={() => {
            console.log(history);
          }}
          span={8}
        >
          {OldValues.map((val, index) => {
            return (
              <>
                <Title key={index}>{t(`${val.Label}`)}:</Title>
                <Content key={index}>
                  {t("OldValues")}:{val.Value}
                </Content>
              </>
            );
          })}
        </Col>
        <Col span={8}>
          {NewValues.map((val, index) => {
            return (
              <>
                <br></br>
                <Content key={index}>
                  {t("NewValues")}:{val.Value}
                </Content>
              </>
            );
          })}
        </Col>
        <Col span={24}>
          <Title>{t("Comments")}:</Title>
          <Content>{Comment}</Content>
        </Col>
      </Row>
    </CustomPanel>
  );
};

HistoryItem.propTypes = {
  bgColor: PropTypes.string,
  history: PropTypes.any,
};

const Title = styled.span`
  color: #253342;
  font-size: 14px;
  font-weight: bold;
`;
const Content = styled.p`
  color: #4d4d4d;
  max-width: 400px !important;
  min-width: 100px;
`;
export default HistoryItem;
