import React, { useEffect, useState } from "react";
import { Form, Button, Col, Row, Skeleton, Input, message } from "antd";
import styled from "styled-components";
import PermissionTable from "../../../../components/Table/PermissonTable";
import { useTranslation } from "react-i18next";
import { useParams } from "react-router-dom";

// Services
import {
  GroupsWithPermissions,
  CreateAttributeByModel,
} from "../../../../services/permissions";

const UserGroupPermissions = () => {
  const [t] = useTranslation();
  const { attributeId } = useParams();
  const [groupPermission, setGroupPermissionList] = useState([]);
  const [loading, setLoading] = useState(true);
  const [filters, setFilters] = useState({});
  const [listPermission, setListPermission] = useState({});
  const [pagination, setPagination] = useState({
    current: 1,
    pageSize: 10,
  });

  const getColumns = () => [
    {
      title: t("Name"),
      dataIndex: "Name",
      width: "45%",
    },
  ];

  const getGroupPermissionList = async (pagination) => {
    const { PageSize, CurrentPage, data } = pagination;
    setLoading(true);

    try {
      const result = await GroupsWithPermissions({
        AttributeId: attributeId,
        PageSize,
        CurrentPage: CurrentPage - 1,
        ...filters,
      });

      setGroupPermissionList(result.data.Value.data);
      setListPermission(result.data.Value.data);
      setPagination({ ...data, total: result.data.Value.recordsTotal });
      setLoading(false);
    } catch (err) {
      setLoading(false);
    }
  };

  const onFinish = async (values) => {
    message.loading({ content: "Loading...", key: "onFinish" });
    try {
      await CreateAttributeByModel({
        Id: attributeId,
        GroupPermissions: listPermission,
      });
      message.success({ content: "Saved!", key: "onFinish" });
    } catch (err) {
      message.error({ content: "There is error!", key: "onFinish" });
    }
  };

  const filterChange = (filter) => {
    const { target } = filter;
    setFilters({ ...filters, [target.name]: target.value });
  };

  const handlePermissionsChange = (value) => {
    setListPermission(
      value.reduce((result, item) => {
        result.push({
          HasEdit: item.HasEdit,
          HasView: item.HasView,
          UserGroupId: item.Id,
        });

        return result;
      }, [])
    );
  };

  const handleTableChange = async (pagination) => {
    const { current, pageSize } = pagination;
    await getGroupPermissionList({
      PageSize: pageSize,
      CurrentPage: current,
      data: pagination,
    });
  };

  const skeletonWrapper = () => {
    return <Skeleton active />;
  };

  useEffect(() => {
    getGroupPermissionList({
      PageSize: 10,
      CurrentPage: 1,
    });
  }, [filters]);

  return (
    <Form layout="vertical" name="basic" onFinish={onFinish}>
      <LeftTabsContentWrapper>
        <Row justify="end" gutter={8}>
          <Col>
            <Button htmlType="submit" type="primary">
              {t("Save Permissions")}
            </Button>
          </Col>
        </Row>
        {loading && skeletonWrapper()}
        {!loading && (
          <PermissionTable
            columns={getColumns()}
            dataSource={groupPermission}
            onReset={() => setFilters({})}
            pagination={pagination}
            scroll={{ x: "1000px" }}
            onChangePermissions={handlePermissionsChange}
            onChange={handleTableChange}
            filters={
              <Input
                name="Name"
                placeholder={t("Name")}
                value={filters.groupname}
                style={{ width: 200 }}
                onChange={(val) => filterChange(val)}
              />
            }
          />
        )}
      </LeftTabsContentWrapper>
    </Form>
  );
};

export default UserGroupPermissions;

const LeftTabsContentWrapper = styled.div`
  padding: 10px;
`;
