import React, { useState, useEffect } from "react";
import { useTranslation } from "react-i18next";
import { Row, Col, Card, Space, Input, InputNumber } from "antd";
import { useParams } from "react-router-dom";

// Components
import DefaultTable from "../../../../components/Table/DefaultTable";
import DatePicker from "../../../../components/DatePicker";
import moment from "moment";

// Services
import { GetHistoryAttribute } from "../../../../services/attributes";

const History = () => {
  const [t] = useTranslation();
  const { attributeId } = useParams();
  const [history, setUserHistory] = useState([]);
  const [loading, setLoading] = useState(true);
  const [filters, setFilters] = useState({});
  const [pagination, setPagination] = useState({
    current: 1,
    pageSize: 10,
  });

  const getColumns = () => [
    {
      title: t("Version"),
      dataIndex: "Version",
      key: "Version",
    },
    {
      title: t("Author"),
      dataIndex: "UserName",
      key: "UserName",
    },
    {
      title: t("Loggedat"),
      dataIndex: "LoggedAt",
      key: "LoggedAt",
    },
    {
      title: t("Old Values"),
      dataIndex: "OldValues",
      key: "OldValues",
      render(text, record) {
        let oldValues = "";

        record.OldValues.forEach(function (item) {
          oldValues += item.Label + ", ";
        });

        return <label>{oldValues}</label>;
      },
    },
    {
      title: t("New Values"),
      dataIndex: "NewValues",
      key: "NewValues",
      render(text, record) {
        let newValues = "";

        record.NewValues.forEach(function (item) {
          newValues += item.Label + ", ";
        });

        return <label>{newValues}</label>;
      },
    },
    {
      title: t("Comment"),
      dataIndex: "Comment",
      key: "Comment",
    },
  ];

  const getUsersHistory = async (pagination) => {
    const { PageSize, CurrentPage, data } = pagination;
    setLoading(true);

    try {
      const result = await GetHistoryAttribute({
        Id: attributeId,
        PageSize,
        CurrentPage: CurrentPage - 1,
        ...filters,
      });

      setUserHistory(result.data.Value.data);
      setPagination({ ...data, total: result.data.Value.recordsTotal });
      setLoading(false);
    } catch (err) {
      setLoading(false);
    }
  };

  const filterChange = (filter) => {
    const { target } = filter;
    setFilters({ ...filters, [target.name]: target.value });
  };

  const filterChangeDate = (filter, dateString, name) => {
    var date = moment(dateString, "YYYY-MM-DD");
    const { target } = filter;
    // setFilters({ ...filters, [name]: date.format("YYYY-MM-DD") });
  };

  const handleTableChange = async (pagination) => {
    const { current, pageSize } = pagination;
    await getUsersHistory({
      PageSize: pageSize,
      CurrentPage: current,
      data: pagination,
      ...filters,
    });
  };

  useEffect(() => {
    getUsersHistory({
      PageSize: 10,
      CurrentPage: 1,
      data: pagination,
    });
  }, [filters]);

  return (
    <Row>
      <Col span={24}>
        <DefaultTable
          columns={getColumns()}
          onReset={() => setFilters({})}
          dataSource={history}
          loading={loading}
          style={{ marginTop: "15px" }}
          pagination={pagination}
          scroll={{ x: "1000px" }}
          onChange={handleTableChange}
          filters={
            <Space>
              <Input
                type="number"
                name="version"
                placeholder={t("Version Start")}
                value={filters.version}
                style={{ width: 125 }}
                onChange={(e) => filterChange(e)}
              />

              <Input
                type="number"
                name="version_end"
                placeholder={t("Version End")}
                value={filters.version_end}
                style={{ width: 120 }}
                onChange={(val) => filterChange(val)}
              />

              <Input
                name="Author"
                placeholder={t("Author")}
                value={filters.Author}
                style={{ width: 120 }}
                onChange={(val) => filterChange(val)}
              />

              <DatePicker
                type="date"
                name="loggedAt"
                format={"DD-MM-YYYY"}
                value={filters.loggedAt}
                placeholder={t("LoggedAt")}
                style={{ width: 120 }}
                onChange={(val, dateString) =>
                  filterChangeDate(val, dateString, "loggedAt")
                }
              />

              <Input
                name="OldValues"
                placeholder={t("Old Values")}
                value={filters.NewValues}
                style={{ width: 120 }}
                onChange={(val) => filterChange(val)}
              />

              <Input
                name="NewValues"
                placeholder={t("New Values")}
                value={filters.OldValues}
                style={{ width: 120 }}
                onChange={(val) => filterChange(val)}
              />
            </Space>
          }
        />
      </Col>
    </Row>
  );
};

History.propTypes = {};

export default History;
