import { privateAxiosInstance } from "./api";
import {
  GET_GROUPS_WITH_PERMISSIONS,
  GET_USERS_WITH_PERMISSIONS,
  UPDATE_ATTR_PERMISSIONS,
} from "../consts/api";

export async function GroupsWithPermissions(filter = {}) {
  return await privateAxiosInstance.post(GET_GROUPS_WITH_PERMISSIONS, filter);
}

export async function UsersWithPermissions(filter = {}) {
  return await privateAxiosInstance.post(GET_USERS_WITH_PERMISSIONS, filter);
}

export async function CreateAttributeByModel(model) {
  return await privateAxiosInstance.post(UPDATE_ATTR_PERMISSIONS, model);
}
