import React, { useState, useEffect } from "react";
import { useTranslation } from "react-i18next";
import {
  Row,
  Col,
  Steps,
  Skeleton,
  Form,
  Space,
  Card,
  Button,
  message,
} from "antd";
import styled from "styled-components";
import { useParams, useHistory } from "react-router-dom";

// Components
import DefaultLayout from "../../../components/Layouts/Default";
import Container from "../../../components/Layouts/Container";
import BreadcrumbNav from "../../../components/BreadcrumbNav";
import ItemLine from "../../../components/Shared/ItemLine";
import CreateBar from "../../../components/Shared/CreateBar";
import Properties from "./tabs/properties";
import Users from "./tabs/users";

// Services
import { CreateGroupByModel } from "../../../services/groups";

export default function NewGroup() {
  const [t] = useTranslation();
  const [form] = Form.useForm();
  const history = useHistory();
  const [loading, setLoading] = useState(true);
  const [step, setStep] = useState(0);
  const [values, setValues] = useState({});
  const [checkedList, setCheckedList] = React.useState([]);

  const getContent = () => {
    switch (step) {
      case 0:
        return <Properties />;
      case 1:
        return <Users />;
      default:
        return <Properties />;
    }
  };

  const next = () => {
    form
      .validateFields()
      .then((formValues) => {
        debugger;
        setValues({
          ...values,
          ...formValues,
        });
        setStep(step + 1);
      })
      .catch((err) => console.log(err));
  };

  const prev = () => {
    if (step != 0) {
      setStep(step - 1);
    }
  };

  const createGroup = async (group) => {
    try {
      await CreateGroupByModel(group);
    } catch (e) {
      console.log(e);
    }
  };

  const onFinish = async () => {
    debugger;
    setLoading(true);
    try {
      await createGroup(values);
      form.resetFields();
      setStep(0);
      message.success("Attribute Başarıyla oluşturuldu");
      // history.push("/attributes");
      setLoading(false);
    } catch (err) {
      message.success(err);
      setLoading(false);
    }
  };

  const skeletonWrapper = () => {
    return <Skeleton active />;
  };

  useEffect(() => {
    setLoading(false);
  }, []);

  return (
    <DefaultLayout>
      <Container>
        <BreadcrumbNav
          navs={[
            { label: "System" },
            { label: "Groups", path: "/system/groups" },
          ]}
        />
      </Container>

      <ItemLine>
        <Container>
          <CreateBar title={t("createGroup")} />
        </Container>
      </ItemLine>

      <Container>
        <Row style={{ marginTop: "20px" }}>
          <Col span={24}>
            <Card>
              <StyledSteps current={step}>
                <Steps.Step key={0} title={t("General")} />
                <Steps.Step key={1} title={t("Users")} />
              </StyledSteps>

              <StepContent>
                <Form layout="vertical" form={form} onFinish={onFinish}>
                  {loading ? skeletonWrapper() : getContent()}

                  <Form.Item>
                    <Row>
                      <Col flex="auto" />
                      <Col>
                        <Space align="center">
                          {step != 0 && (
                            <Button type="primary" onClick={prev}>
                              {t("Previous")}
                            </Button>
                          )}
                          {step != 1 && (
                            <Button type="primary" onClick={next}>
                              {t("Next")}
                            </Button>
                          )}
                          {step == 1 && (
                            <Button type="primary" htmlType="submit">
                              {t("Finish")}
                            </Button>
                          )}
                        </Space>
                      </Col>
                    </Row>
                  </Form.Item>
                </Form>
              </StepContent>
            </Card>
          </Col>
        </Row>
      </Container>
    </DefaultLayout>
  );
}

const StyledSteps = styled(Steps)`
  padding: 20px;
`;

const StepContent = styled.div`
  padding: 20px;
`;
