import React, { useState, useEffect } from "react";
import { useDispatch } from "react-redux";
import { useTranslation } from "react-i18next";
import { DragDropContext, Droppable, Draggable } from "react-beautiful-dnd";
import { tableData, treeData } from "../../mockData";
import { CKEditor } from "@ckeditor/ckeditor5-react";
import ClassicEditor from "@ckeditor/ckeditor5-build-classic";

// Components
import Layout from "../../components/turkcell/Layout";
import Content from "../../components/turkcell/Content";
import Filter from "../../components/turkcell/TemplateCreateFilter";
import { Row, Col, Tabs, Modal, Button, Card } from "antd";
import CustomPanel from "../../components/turkcell/CustomPanel";

import { getItems, toggleFavItem } from "../../services/turkcell/items";

export default function Create() {
  const { TabPane } = Tabs;
  const { t } = useTranslation();
  const [isModalVisible, setIsModalVisible] = useState(false);
  const [isRowVisible, setIsRowVisible] = useState("hidden");
  const [items, setItems] = useState([]);
  const [droppable1, setDroppable1] = useState(treeData);

  const [droppable2, setDroppable2] = useState([]);

  const [filters, setFilters] = useState(null);
  const navs = [
    {
      label: t("Templates/create"),
      path: "/templates/75/create",
    },
  ];

  useEffect(() => {
    return console.log(droppable1);
  });

  const showModal = () => {
    setIsModalVisible(true);
  };

  const handleOk = () => {
    setIsModalVisible(false);
  };

  const handleCancel = () => {
    setIsModalVisible(false);
  };

  const onDragEnd = (result) => {
    console.log(result.source.index);
    if (!result.destination) return;
    //droppable1 den droppable 2 ye aktarma
    if (
      result.source.droppableId === "droppable1" &&
      result.destination.droppableId === "droppable2"
    ) {
      if (droppable2.length === 0) {
        setDroppable2(
          droppable1.filter((droppable1) =>
            droppable1.key.includes(result.draggableId)
          )
        );
        setDroppable1(
          droppable1.filter(
            (droppable1) => droppable1.key !== result.draggableId
          )
        );
      } else {
        //droppable2 statetine değer ekler
        setDroppable2([...droppable2, droppable1[result.source.index]]);
        setDroppable1(
          droppable1.filter(
            (droppable1) => droppable1.key !== result.draggableId
          )
        );
      }
    }
    //droppable 2 den droppable 1 e aktarma
    if (
      result.source.droppableId === "droppable2" &&
      result.destination.droppableId === "droppable1"
    ) {
      if (droppable1.length === 0) {
        setDroppable1(
          droppable2.filter((droppable2) =>
            droppable2.key.includes(result.draggableId)
          )
        );
        setDroppable2(
          droppable2.filter(
            (droppable2) => droppable2.key !== result.draggableId
          )
        );
      } else {
        //droppable1 statetine değer ekler
        setDroppable1([...droppable1, droppable2[result.source.index]]);
        setDroppable2(
          droppable2.filter(
            (droppable2) => droppable2.key !== result.draggableId
          )
        );
      }
    }
    //droppable2 içerisinde siralama
    if (
      result.source.droppableId === "droppable2" &&
      result.destination.droppableId === "droppable2"
    ) {
      const items = Array.from(droppable2);
      const [reorderedItem] = items.splice(result.source.index, 1);
      items.splice(result.destination.index, 0, reorderedItem);

      setDroppable2(items);
    }
    //droppable1 içerisinde siralama
    if (
      result.source.droppableId === "droppable1" &&
      result.destination.droppableId === "droppable1"
    ) {
      const items = Array.from(droppable1);
      const [reorderedItem] = items.splice(result.source.index, 1);
      items.splice(result.destination.index, 0, reorderedItem);

      setDroppable1(items);
    }
  };

  const handleOnFinish = (values) => {
    getItemFromBack();
    setIsRowVisible("visible");

    setFilters(values);
    console.log(filters);
  };

  const callback = (key) => {
    console.log(key);
  };

  const getItemFromBack = async () => {
    try {
      const itemsRes = await getItems({
        OrderedColumns: ["MADDE_ADI", "ItemStatusesId", "MADDE_METNI"],
        ItemType: "Clause",
        ChannelId: 2,
        SelectedCategoryId: 0,
        IsPagingEnabled: false,
      });
      console.log(itemsRes);
      setItems(itemsRes);
    } catch (err) {
      console.log(err);
    }
  };

  return (
    <Layout>
      <Content navs={[...navs]}>
        <Filter onFinish={handleOnFinish} onClear={() => setFilters(null)} />
        <DragDropContext onDragEnd={onDragEnd}>
          <Row
            style={{
              marginTop: "1em",
              padding: "0px",
              visibility: isRowVisible,
            }}
          >
            <Col
              style={{
                borderRadius: "5px",
                border: "1px solid rgb(220 220 220)",
                padding: "1em",
                backgroundColor: "rgb(255,255,255)",
              }}
              span={7}
            >
              <Tabs defaultActiveKey="1" onChange={callback}>
                <TabPane tab="Tab 1" key="1">
                  <Droppable droppableId="droppable1">
                    {(provided) => (
                      <div ref={provided.innerRef} {...provided.droppableProps}>
                        {droppable1.map(({ key, title, children }, index) => {
                          return (
                            <Draggable
                              key={key}
                              draggableId={key}
                              index={index}
                            >
                              {(provided) => (
                                <div
                                  ref={provided.innerRef}
                                  {...provided.draggableProps}
                                  {...provided.dragHandleProps}
                                >
                                  <CustomPanel key={key} label={title}>
                                    {children.map(({ key, title }, index) => {
                                      return (
                                        <>
                                          <div
                                            style={{
                                              backgroundColor:
                                                index % 2 == 0
                                                  ? "#fafafa"
                                                  : "#ffffff",
                                            }}
                                            key={key}
                                            id={index}
                                          >
                                            {title}
                                          </div>
                                        </>
                                      );
                                    })}
                                  </CustomPanel>
                                </div>
                              )}
                            </Draggable>
                          );
                        })}
                        {provided.placeholder}
                      </div>
                    )}
                  </Droppable>
                </TabPane>
                <TabPane tab="Tab 2" key="2">
                  Content of Tab Pane 2
                </TabPane>
                <TabPane tab="Tab 3" key="3">
                  Content of Tab Pane 3
                </TabPane>
              </Tabs>
            </Col>
            <Col span={1}></Col>
            <Droppable droppableId="droppable2">
              {(provided) => (
                <Col
                  ref={provided.innerRef}
                  {...provided.droppableProps}
                  onClick={() => {
                    console.log(droppable2);
                  }}
                  style={{
                    padding: "1em",
                    borderRadius: "5px",
                    border: "1px solid rgb(220 220 220)",
                    backgroundColor: "rgb(255,255,255)",
                  }}
                  span={16}
                >
                  {droppable2.map(({ key, title, children }, index) => {
                    return (
                      <Draggable key={key} draggableId={key} index={index}>
                        {(provided) => (
                          <div
                            ref={provided.innerRef}
                            {...provided.draggableProps}
                            {...provided.dragHandleProps}
                          >
                            <div>
                              {children.map(({ key, title }, index) => {
                                return (
                                  <>
                                    <Row>
                                      <Col span={4}></Col>
                                      <Col span={16} key={key} id={index}>
                                        <Card
                                          title="Default size card"
                                          extra={
                                            <Button
                                              type="primary"
                                              onClick={showModal}
                                            >
                                              Open Modal
                                            </Button>
                                          }
                                          style={{ width: "auto" }}
                                        >
                                          {title}
                                        </Card>

                                        <Modal
                                          title="Basic Modal"
                                          visible={isModalVisible}
                                          onOk={handleOk}
                                          onCancel={handleCancel}
                                        >
                                          <CKEditor
                                            editor={ClassicEditor}
                                            data={title}
                                          />
                                        </Modal>
                                      </Col>
                                      <Col span={4}></Col>
                                    </Row>
                                  </>
                                );
                              })}
                            </div>
                          </div>
                        )}
                      </Draggable>
                    );
                  })}
                  {provided.placeholder}
                </Col>
              )}
            </Droppable>
          </Row>
        </DragDropContext>
      </Content>
    </Layout>
  );
}
